/* Framework imports -------------------------------------------------------- */
import React, {
  useEffect,
  useRef,
  useState,
} from 'react'
import styled from '@emotion/styled'

/* Module imports ----------------------------------------------------------- */
import {
  useAppDispatch,
  useAppSelector,
} from 'store/hooks'
import {
  selectSavedMakers,
  setSavedMakers,
} from 'store/slices/makerSlice'

/* Component imports -------------------------------------------------------- */
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from '@mui/material'
import {
  CloudUpload,
  ContentCopy,
  Delete,
  Download,
  Edit,
} from '@mui/icons-material'
import {
  ReactCrop,
  type Crop,
} from 'react-image-crop'
import LongButton from 'components/LongButton/LongButton'
import CustomIconButton from 'components/IconButtons/CustomIconButton/CustomIconButton'
import FormBoldTitle from 'components/BoldTitle/FormBoldTitle'
import BoldTitle from 'components/BoldTitle/BoldTitle'
import { selectImgurToken } from 'store/slices/imgurSlice'
import { toast } from 'react-toastify'
import type { ImgurUploadResponse } from 'types/Imgur'

/* Styled components -------------------------------------------------------- */
const ImageProcessorContainer = styled.div`
  width: 100%;

  .FIE_rotate-tool-button {
    display: none;
  }
`

const TitleButtons = styled.div`
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
`

const ModalTitle = styled(DialogTitle)`
  text-align: center;
  color: ${(props) => props.theme.palette.secondary.main};
  font-weight: bold;
`

const ModalAction = styled(DialogActions)`
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
`

const Container = styled.div`
  max-height: calc(100vh - 230px);
  display: flex;
  justify-content: center;

  img {
    max-height: calc(100vh - 230px);
  }
`

/* Component declaration ---------------------------------------------------- */
interface ImageMakerProps {
  id: number;
  onDeleteContainer: () => void;
  name: string;
  onChangeName: (value: string) => void;
}

const ImageMaker: React.FC<ImageMakerProps> = ({
  id,
  onChangeName,
  name,
  onDeleteContainer,
}) => {
  const defaultUrl = process.env.NODE_ENV === 'production' ? '' : 'https://static.zerochan.net/Mahou.Shoujo.Madoka%E2%98%86Magica.full.3522716.jpg'
  const dispatch = useAppDispatch()
  const savedMakers = useAppSelector(selectSavedMakers)
  const tokens = useAppSelector(selectImgurToken)
  const imgRef = useRef<HTMLImageElement | null>(null)
  const [ openInput, setOpenInput ] = useState<boolean>(!savedMakers.some((val) => val.id === id))
  const [ input, setInput ] = useState<string>(savedMakers.find((val) => val.id === id)?.imageUrl || '')
  const [ newImage, setNewImage ] = useState<string>(savedMakers.find((val) => val.id === id)?.imageUrl || '')
  const [ crop, setCrop ] = useState<Crop>({ unit: 'px', width: 225, height: 350, x: 0, y: 0 })
  const [ isUploading, setIsUploading ] = useState<boolean>(false)
  const [ link, setLink ] = useState<string>('')

  useEffect(() => {
    dispatch(setSavedMakers(savedMakers.map((value) => value.id === id ? { ...value, imageUrl: input } : value)))
  }, [ input ])

  const resizeImage = () => {
    if (imgRef.current) {
      const { width: imgWidth, height: imgHeight } = imgRef.current
      const aspectRatio = 225 / 350
      let cropWidth = imgWidth
      let cropHeight = imgHeight

      if (cropWidth / cropHeight > aspectRatio) {
        cropWidth = cropHeight * aspectRatio
      } else {
        cropHeight = cropWidth / aspectRatio
      }

      setCrop({
        unit: 'px',
        x: (imgWidth - cropWidth) / 2,
        y: (imgHeight - cropHeight) / 2,
        width: cropWidth,
        height: cropHeight,
      })
    }
  }

  useEffect(() => {
    resizeImage()
    const observer = new ResizeObserver(resizeImage)
    if (imgRef.current) {
      observer.observe(imgRef.current)
    }

    return () => {
      if (imgRef.current) {
        observer.unobserve(imgRef.current)
      }
      observer.disconnect()
    }
  }, [])

  const onClickLoad = () => {
    setOpenInput(false)
    setInput(newImage)
  }

  const onCloseModal = () => {
    setOpenInput(false)
    if (!name && !input) {
      onDeleteContainer()
    }
  }

  const onCopyToClipBoard = async () => {
    console.log('heyy')
    const value = `$ai ${name}$${link}`

    if (typeof ClipboardItem !== 'undefined') {
      console.log('yo')
      const html = new Blob([ value ], { type: 'text/html' })
      const text = new Blob([ value ], { type: 'text/plain' })
      const data = new ClipboardItem({ 'text/html': html, 'text/plain': text })

      console.log(data)
      await navigator.clipboard.write([ data ])
    }
  }

  const downloadCroppedImage = (mode: 'upload' | 'download') => {
    const image = new Image()
    image.crossOrigin = 'anonymous'
    image.referrerPolicy = 'no-referrer'
    image.src = input

    image.onload = () => {
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')

      if (!imgRef.current || !ctx) return

      const scaleX = image.naturalWidth / imgRef.current.width
      const scaleY = image.naturalHeight / imgRef.current.height
      const offscreen = new OffscreenCanvas(crop.width * scaleX, crop.height * scaleY)

      canvas.width = offscreen.width
      canvas.height = offscreen.height

      ctx.drawImage(
        image,
        crop.x * scaleX,
        crop.y * scaleY,
        offscreen.width,
        offscreen.height,
        0,
        0,
        offscreen.width,
        offscreen.height,
      )

      if (mode === 'download') {
        canvas.toBlob((blob) => {
          if (blob) {
            const url = URL.createObjectURL(blob)
            const a = document.createElement('a')
            a.href = url
            a.download = `${name}.png`
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
            URL.revokeObjectURL(url)
          }
        }, 'image/png')
      } else {
        const data = canvas.toDataURL('image/png').split(';base64,')[1]
        const formData = new FormData()
        formData.append('image', data)
        formData.append('title', name)
        formData.append('type', 'base64')
        formData.append('description', 'Imported with Mudae Tools')

        setIsUploading(true)
        fetch(`https://api.imgur.com/3/image`, {
          method: 'POST',
          headers: { 'Authorization': `Bearer ${tokens.accessToken}` },
          body: formData,
        }).then(async (response) => {
          const res = await response.json() as ImgurUploadResponse
          setLink(res.data.link)
          onCopyToClipBoard()
          toast.success('Image was upload and command copied to your clipboard.')
        }).catch((error) => {
          console.error(error)
          toast.error('Error, could not upload image.')
        }).finally(() => setIsUploading(false))
      }
    }
  }

  return (
    <ImageProcessorContainer>
      <BoldTitle>
        {name}
        <TitleButtons>
          <CustomIconButton
            onClick={() => setOpenInput(true)}
            variant="contained"
            Icon={Edit}
            label="Input image"
          />
          <CustomIconButton
            onClick={() => downloadCroppedImage('download')}
            variant="contained"
            Icon={Download}
            label="Download image"
          />
          <CustomIconButton
            onClick={() => downloadCroppedImage('upload')}
            variant="contained"
            Icon={CloudUpload}
            label="Upload image"
            disabled={isUploading}
          />
          <CustomIconButton
            onClick={onCopyToClipBoard}
            variant="contained"
            Icon={ContentCopy}
            label="Copy last generated command"
            disabled={isUploading || !link}
          />
          <CustomIconButton
            Icon={Delete}
            variant="outlined"
            color="error"
            onClick={onDeleteContainer}
            label="Remove image"
          />
        </TitleButtons>
      </BoldTitle>
      <Dialog
        open={openInput}
        onClose={onCloseModal}
        maxWidth="md"
        fullWidth
      >
        <ModalTitle>
          Add image
        </ModalTitle>
        <DialogContent>
          <FormBoldTitle>
            Character's name (as displayed in Mudae)
          </FormBoldTitle>
          <TextField
            value={name}
            placeholder="Madoka Kaname"
            onChange={(e) => onChangeName(e.target.value)}
            size="small"
          />
          <FormBoldTitle>
            Image's URL (right-click on image + "copy image's address")
          </FormBoldTitle>
          <TextField
            value={newImage}
            placeholder={defaultUrl}
            onChange={(e) => setNewImage(e.target.value)}
            size="small"
          />
        </DialogContent>
        <ModalAction>
          <LongButton
            onClick={onCloseModal}
            variant="outlined"
          >
            Cancel
          </LongButton>
          <LongButton
            onClick={onClickLoad}
            variant="contained"
            disabled={!input || !name}
          >
            Load image
          </LongButton>
        </ModalAction>
      </Dialog>
      <Container>
        {
          input &&
            <ReactCrop
              crop={crop}
              onChange={(c) => setCrop(c)}
              aspect={225 / 350}
            >
              <img
                ref={imgRef}
                src={input}
                referrerPolicy="no-referrer"
              />
            </ReactCrop>
        }
      </Container>
    </ImageProcessorContainer>
  )
}

export default ImageMaker
